* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #222;
  font-family: 'Space Mono', monospace;
  color: white;
  letter-spacing: 2px;
}

*::-moz-selection {
  background-color: transparent;
}

*::selection {
  background-color: transparent;
}

h1,
h2,
h3,
p {
  cursor: default;
  font-weight: normal;
}

@media (min-width: 568px) and (orientation: landscape) {
  #root {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
  }

  #root div {
    order: 2;
  }
}

@media (min-width: 1024px) and (orientation: landscape) {
  #root {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
  }

  #root div {
    order: 2;
  }
}
.gameController {
  position: absolute;
  top: 0;
  left: 0;
  padding: 5vh;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, .8);
  overflow: hidden;
  z-index: 5;
}

.gameController div h2 {
  line-height: 60px;
  text-align: center;
  margin: 20vh 0 20px;
}

.gameController div p {
  line-height: 30px;
  font-size: 16px;
  text-align: center;
}

.gameController button {
  position: absolute;
  bottom: 10%;
  left: 50%;
  -webkit-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);
  height: 50px;
  width: 250px;
  background-color: rgba(30, 144, 255, .5);
  font-size: 16px;
  color: white;
  text-transform: uppercase;
  letter-spacing: 1px;
  border: none;
  border-radius: 25vw;
}

.gameController button:active,
.gameController button:focus {
  outline: none;
  background-color: dodgerblue;
}

@media (min-width: 768px) and (orientation: portrait) {

  .gameController button {
    width: 50vw;
    height: 10vh;
    font-size: 22px;
  }
}

@media (min-width: 1024px) and (orientation: landscape) {

  .gameController button {
    width: 35vw;
    height: 7vh;
    font-size: 22px;
  }
}
.board {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 100vw;
  height: 100vw;
  background-color: rgba(30, 50, 255, .5);
  overflow: hidden;
  z-index: 1;
}

.board::after {
  content: "";
  position: absolute;
  top: calc(50% + (100vw / 14));
  left: calc(50% + (100vw / 14));
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: block;
  width: 0px;
  height: 0px;
  background-color: transparent;
  border: 5px solid dodgerblue;
  border-radius: 50%;
  box-shadow: 0 0 10px 5px dodgerblue;
  -webkit-animation: radar 4s infinite linear;
          animation: radar 4s infinite linear;
  z-index: -1;
}

.boardLetter,
.boardRow,
.boardCell {
  width: calc(100vw / 7);
  height: calc(100vw / 7);
  border: 1px solid dodgerblue;
  line-height: calc(100vw / 7);
  text-align: center;
}

.boardLetter,
.boardRow {
  background-color: #025;
}

.hit {
  background-color: transparent;
  background-image: url(/battleship/static/media/hit.29f0b336.png);
  background-size: contain;
}

.miss {
  background-color: transparent;
  background-image: url(data:image/png;base64,);
  background-size: contain;
}

.sunk {
  background-color: rgba(0, 0, 50, .3);
}

@-webkit-keyframes radar {
  0% {
    width: 0;
    height: 0;
    opacity: 1;
  }

  90% {
    width: 90vh;
    height: 90vh;
    opacity: .5;
  }

  100% {
    width: 100vh;
    height: 100vh;
    opacity: 0;
  }
}

@keyframes radar {
  0% {
    width: 0;
    height: 0;
    opacity: 1;
  }

  90% {
    width: 90vh;
    height: 90vh;
    opacity: .5;
  }

  100% {
    width: 100vh;
    height: 100vh;
    opacity: 0;
  }
}

@media (min-width: 768px) and (orientation: portrait) {
  .board::after {
    content: "";
    position: absolute;
    top: calc(50% + (100vw / 22));
    left: calc(50% + (100vw / 22));
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: block;
    width: 0px;
    height: 0px;
    background-color: transparent;
    border: 5px solid dodgerblue;
    border-radius: 50%;
    box-shadow: 0 0 10px 5px dodgerblue;
    -webkit-animation: radar 4s infinite linear;
            animation: radar 4s infinite linear;
    z-index: -1;
  }

  .boardLetter,
  .boardRow,
  .boardCell {
    width: calc(100vw / 11);
    height: calc(100vw / 11);
    border: 1px solid dodgerblue;
    line-height: calc(100vw / 11);
    text-align: center;
  }
}

@media (orientation: landscape) {
  .board {
    order: 1;
    width: 90vh;
    height: 90vh;
  }

  .board::after {
    top: calc(50% + (90vh / 14));
    left: calc(50% + (90vh / 14));
  }

  .boardLetter,
  .boardRow,
  .boardCell {
    width: calc(90vh / 7);
    height: calc(90vh / 7);
    line-height: calc(90vh / 7);
  }

  .boardCell {
    cursor: url(data:image/png;base64,) 25 25, default;
  }

  @-webkit-keyframes radar {
    0% {
      width: 0;
      height: 0;
      opacity: 1;
    }

    90% {
      width: 90vw;
      height: 90vw;
      opacity: .5;
    }

    100% {
      width: 100vw;
      height: 100vw;
      opacity: 0;
    }
  }

  @keyframes radar {
    0% {
      width: 0;
      height: 0;
      opacity: 1;
    }

    90% {
      width: 90vw;
      height: 90vw;
      opacity: .5;
    }

    100% {
      width: 100vw;
      height: 100vw;
      opacity: 0;
    }
  }

}

@media (min-width: 1024px) and (orientation: landscape) {
  .board {
    order: 1;
    width: 90vh;
    height: 90vh;
  }

  .board::after {
    top: calc(50% + (90vh / 22));
    left: calc(50% + (90vh / 22));
  }

  .board p {
    font-size: 25px;
  }

  .boardLetter,
  .boardRow,
  .boardCell {
    width: calc(90vh / 11);
    height: calc(90vh / 11);
    line-height: calc(90vh / 11);
  }
}
.message {
  width: 100vw;
  line-height: 40px;
  padding: 10px 0;
  text-align: center;
  background-color: rgba(30, 144, 255, .5);
  border: 1px solid dodgerblue;
}

.message h2 {
  font-size: 18px;
}

.stats {
  position: relative;
  width: 100vw;
  height: calc(100vh - 100vw - 80px);
  padding: 10px 5vw;
  text-align: center;
}

.stats h3 {
  padding: 10px;
}


.stats h3:last-of-type {
  margin-left: -5vw;
  width: 100vw;
  background-color: rgba(30, 144, 255, .5);
  border: 1px solid dodgerblue;
}

.stats h3 span {
  color: white;
}

.sunkenShips {
  min-height: 150px;
}

.sunkenShips img {
  position: relative;
  max-width: 5vw;
  margin: 10px;
}

@media (min-width: 768px) and (orientation: portrait) {

  .stats h3 {
    width: 50vw;
    text-align: left;
    line-height: 80px;
    font-size: 25px;
    float: left;
  }

  .stats h3:last-of-type {
    position: absolute;
    bottom: 0;
    left: calc(10vw / 2);
    width: auto;
    margin: 0;
    background-color: transparent;
    border: none;
  }

  .sunkenShips img {
    max-width: 3vh;
  }
}

@media (min-width: 568px) and (orientation: landscape) {

  .message,
  .stats {
    width: calc(100vw - 90vh);
  }

  .message {
    height: calc((90vh / 7) * 2);
  }

  .message h2 {
    font-size: 20px;
    letter-spacing: 1px;
    line-height: calc(90vh / 7);
  }

  .stats {
    position: relative;
    height: calc((90vh / 7) * 5);
  }

  .stats h3 {
    font-size: 14px;
    line-height: 40px;
    color: #ccc;
  }

  .stats h3:last-of-type {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0;
    width: auto;
    line-height: calc(90vh / 11);
    background-color: rgba(30, 144, 255, .5);
    border: 1px solid dodgerblue;
    text-align: center;
  }

  .sunkenShips img {
    position: relative;
    max-width: 5vh;
    margin: 10px;
  }
}

@media (min-width: 1024px) and (orientation: landscape) {

  .message,
  .stats {
    width: calc(100vw - 90vh);
  }

  .message {
    height: calc((90vh / 11) * 2);
  }

  .message h2 {
    font-family: Arial;
    font-size: 30px;
    line-height: calc((90vh / 11) * 2);
  }

  .stats {
    position: relative;
    height: calc((90vh / 11) * 9);
  }

  .stats h3 {
    font-size: 20px;
    line-height: 50px;
    color: #ccc;
    letter-spacing: 1px;
  }

  .stats h3:last-of-type {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    line-height: calc(90vh / 11);
    background-color: rgba(30, 144, 255, .5);
    border: 1px solid dodgerblue;
    text-align: center;
  }

  .sunkenShips img {
    position: relative;
    max-width: 5vh;
    margin: 10px;
  }
}
